import { NextRequest, NextResponse } from 'next/server'
import { getUserFromRequest } from '@/lib/auth-helper'
import { prisma } from '@/lib/db'
import { DEMO_SETTINGS } from '@/lib/auth/constants'

export const runtime = 'nodejs'
export const dynamic = 'force-dynamic'

// Only master admin or agency owners can create demo access
export async function POST(request: NextRequest) {
  try {
    const user = await getUserFromRequest(request)

    // Check authentication
    if (!user) {
      return NextResponse.json(
        { error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Check permissions (master admin or agency owner)
    const userRole = user.role
    if (userRole !== 'master_admin' && userRole !== 'agency_owner') {
      return NextResponse.json(
        { error: 'Insufficient permissions' },
        { status: 403 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { error: 'Database not available' },
        { status: 503 }
      )
    }

    const body = await request.json()
    const { name, email, company, phone, internalNotes, withAccessCode } = body

    // Validate required fields
    if (!name || !email) {
      return NextResponse.json(
        { error: 'Name and email are required' },
        { status: 400 }
      )
    }

    // Validate email format
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (!emailRegex.test(email)) {
      return NextResponse.json(
        { error: 'Invalid email format' },
        { status: 400 }
      )
    }

    // Check if email already has active demo access
    const existingDemo = await prisma.demoAccess.findFirst({
      where: {
        email: email.toLowerCase(),
        status: { in: ['pending', 'active'] },
        expiresAt: { gt: new Date() },
      },
    })

    if (existingDemo) {
      // Return existing access
      const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://app.risivo.com'
      return NextResponse.json({
        success: true,
        isExisting: true,
        demoAccess: {
          id: existingDemo.id,
          name: existingDemo.name,
          email: existingDemo.email,
          accessToken: existingDemo.accessToken,
          accessCode: existingDemo.accessCode,
          magicLink: `${baseUrl}/demo/access?token=${existingDemo.accessToken}`,
          expiresAt: existingDemo.expiresAt.toISOString(),
          status: existingDemo.status,
        },
        message: 'Demo access already exists for this email',
      })
    }

    // Calculate expiry date (30 days from now)
    const expiresAt = new Date()
    expiresAt.setDate(expiresAt.getDate() + DEMO_SETTINGS.EXPIRY_DAYS)

    // Generate optional 6-digit access code
    const accessCode = withAccessCode 
      ? Math.floor(100000 + Math.random() * 900000).toString()
      : null

    // Create the demo access record
    const demoAccess = await prisma.demoAccess.create({
      data: {
        name,
        email: email.toLowerCase(),
        company,
        phone,
        accessCode,
        expiresAt,
        createdBy: user.id,
        internalNotes,
        status: 'pending',
      },
    })

    const baseUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://app.risivo.com'
    const magicLink = `${baseUrl}/demo/access?token=${demoAccess.accessToken}`

    return NextResponse.json({
      success: true,
      isExisting: false,
      demoAccess: {
        id: demoAccess.id,
        name: demoAccess.name,
        email: demoAccess.email,
        accessToken: demoAccess.accessToken,
        accessCode: demoAccess.accessCode,
        magicLink,
        expiresAt: demoAccess.expiresAt.toISOString(),
        status: demoAccess.status,
      },
      message: 'Demo access created successfully',
    })
  } catch (error) {
    console.error('Create demo access error:', error)
    return NextResponse.json(
      { error: 'Failed to create demo access' },
      { status: 500 }
    )
  }
}

// Delete a demo access
export async function DELETE(request: NextRequest) {
  try {
    const user = await getUserFromRequest(request)

    if (!user) {
      return NextResponse.json(
        { error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Only master admin can delete demo accesses
    if (user.role !== 'master_admin') {
      return NextResponse.json(
        { error: 'Insufficient permissions' },
        { status: 403 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { error: 'Database not available' },
        { status: 503 }
      )
    }

    const { searchParams } = new URL(request.url)
    const id = searchParams.get('id')

    if (!id) {
      return NextResponse.json(
        { error: 'Demo access ID is required' },
        { status: 400 }
      )
    }

    // Check if demo access exists
    const demoAccess = await prisma.demoAccess.findUnique({
      where: { id },
    })

    if (!demoAccess) {
      return NextResponse.json(
        { error: 'Demo access not found' },
        { status: 404 }
      )
    }

    // Delete associated access logs first (if any)
    await prisma.demoAccessLog.deleteMany({
      where: { demoAccessId: id },
    })

    // Delete the demo access
    await prisma.demoAccess.delete({
      where: { id },
    })

    return NextResponse.json({
      success: true,
      message: 'Demo access deleted successfully',
    })
  } catch (error) {
    console.error('Delete demo access error:', error)
    return NextResponse.json(
      { error: 'Failed to delete demo access' },
      { status: 500 }
    )
  }
}

// List all demo accesses (for admin)
export async function GET(request: NextRequest) {
  try {
    const user = await getUserFromRequest(request)

    if (!user) {
      return NextResponse.json(
        { error: 'Unauthorized' },
        { status: 401 }
      )
    }

    // Only master admin can list all demos
    if (user.role !== 'master_admin') {
      return NextResponse.json(
        { error: 'Insufficient permissions' },
        { status: 403 }
      )
    }

    if (!prisma) {
      return NextResponse.json(
        { error: 'Database not available' },
        { status: 503 }
      )
    }

    const { searchParams } = new URL(request.url)
    const status = searchParams.get('status')

    const where: any = {}
    if (status) {
      where.status = status
    }

    const demoAccesses = await prisma.demoAccess.findMany({
      where,
      orderBy: { createdAt: 'desc' },
      include: {
        _count: {
          select: { accessLogs: true },
        },
      },
    })

    return NextResponse.json({
      demoAccesses: demoAccesses.map(demo => ({
        id: demo.id,
        name: demo.name,
        email: demo.email,
        company: demo.company,
        status: demo.status,
        expiresAt: demo.expiresAt.toISOString(),
        firstAccessAt: demo.firstAccessAt?.toISOString(),
        lastAccessAt: demo.lastAccessAt?.toISOString(),
        accessCount: demo.accessCount,
        logCount: demo._count.accessLogs,
        createdAt: demo.createdAt.toISOString(),
      })),
    })
  } catch (error) {
    console.error('List demo accesses error:', error)
    return NextResponse.json(
      { error: 'Failed to list demo accesses' },
      { status: 500 }
    )
  }
}
