// Master Admin Email (only this email gets master admin privileges)
export const MASTER_ADMIN_EMAIL = 'admin@risivo.com'

// User Roles
export const USER_ROLES = {
  MASTER_ADMIN: 'master_admin',
  AGENCY_OWNER: 'agency_owner',
  AGENCY_ADMIN: 'agency_admin',
  USER: 'user',
  VIEWER: 'viewer',
  DEMO: 'demo',
} as const

export type UserRole = typeof USER_ROLES[keyof typeof USER_ROLES]

// Role Hierarchy (higher number = more permissions)
export const ROLE_HIERARCHY: Record<UserRole, number> = {
  [USER_ROLES.MASTER_ADMIN]: 100,
  [USER_ROLES.AGENCY_OWNER]: 80,
  [USER_ROLES.AGENCY_ADMIN]: 60,
  [USER_ROLES.USER]: 40,
  [USER_ROLES.VIEWER]: 20,
  [USER_ROLES.DEMO]: 30, // Demo has more than viewer but less than user
}

// Account Status
export const ACCOUNT_STATUS = {
  ACTIVE: 'active',
  SUSPENDED: 'suspended',
  PENDING: 'pending',
  DEACTIVATED: 'deactivated',
} as const

export type AccountStatus = typeof ACCOUNT_STATUS[keyof typeof ACCOUNT_STATUS]

// Demo Access Status
export const DEMO_STATUS = {
  PENDING: 'pending',
  ACTIVE: 'active',
  EXPIRED: 'expired',
  REVOKED: 'revoked',
} as const

export type DemoStatus = typeof DEMO_STATUS[keyof typeof DEMO_STATUS]

